// TODO 接口 + 详细表单UI

<template>
<!-- view 预览  -->
  <div class="wrapper view"> 
    <van-form @submit="onSubmit" disabled>
      <div class="form_block">
        <div class="form_title">基本资料 <span class="preview">预览</span></div>
        <div class="form_item">
          <van-field
            v-model="name"
            name="我的姓名"
            label="我的姓名"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="name"
            name="性别"
            label="性别"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="birthday"
            name="出生年月"
            label="出生年月"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="title"
            name="我的职称"
            label="我的职称"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="phoneNumber"
            name="联系电话"
            label="联系电话"
            placeholder="请输入"
            input-align="right"
          />
        </div>
      </div>
      <div class="form_block">
        <div class="form_title">学习经历</div>
        <div class="form_item">
          <van-field
            v-model="name"
            name="我的姓名"
            label="我的姓名"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="degree"
            name="最高学历"
            label="最高学历"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="school"
            name="毕业学校"
            label="毕业学校"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="professional"
            name="专业"
            label="专业"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="inSchoolTime"
            name="在校时间"
            label="在校时间"
            placeholder="请输入"
            input-align="right"
          />
        </div>
      </div>
      <div class="form_block">
        <div class="form_title">工作/实习经历</div>
        <div class="form_item">
          <van-field
            v-model="company"
            name="企业名称"
            label="企业名称"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="position"
            name="职务"
            label="职务"
            placeholder="请输入"
            input-align="right"
          />
          <van-field
            v-model="inCompanyTime"
            name="在职时间"
            label="在职时间"
            placeholder="请输入"
            input-align="right"
          />
        </div>
      </div>
      <div class="form_block">
        <div class="form_title">成功案例介绍</div>
        <div class="form_item">
          <div class="form_textarea_wrap">
            <van-field
              name="自我介绍"
              v-model="intro"
              rows="20"
              autosize
              type="textarea"
              placeholder="请输入"
            />
          </div>
        </div>
      </div>
      <div class="form_block">
        <div class="form_title">自我评价</div>
        <div class="form_item">
          <div class="form_textarea_wrap">
            <van-field
              v-model="evaluation"
              rows="20"
              autosize
              type="textarea"
              placeholder="请输入"
              name="自我评价"
            />
          </div>
        </div>
      </div>

      <div class="submit">
        <van-button type="info" size="large" native-type="submit"
          >保存</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  metaInfo: {
    title: "我的简历",
  },
  data() {
    return {
      name: "111",
      sex: "111",
      birthday: "111",
      title: "111",
      phoneNumber: "111",
      degree: "111",
      school: "111",
      professional: "111",
      inSchoolTime: "111",
      company: "111",
      position: "111",
      inCompanyTime: "111",
      intro: "111",
      evaluation: "111",
    };
  },
  mounted() {},
  methods: {
    onSubmit(value) {
      console.log(value);
    },
  },
  components: {},
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  background-color: #f2f2f2;

  &.view {
    padding-bottom: 20px;

    .submit {
      display: none;
    }

    .preview {
      display: none;
    }

    .form_item {
      .form_textarea_wrap {
        .van-cell {
          background-color: #fff;
          white-space: pre-wrap;
          padding: 0;
        }
      }
    }
  }

  .van-cell {
    .van-field--disabled .van-field__label {
      color: #444;
    }
  }
  .form_item {
    background-color: #fff;

    .van-field--disabled {
      /deep/ .van-cell__title {
        color: #444;
      }

      /deep/ .van-field__control {
        color: #666;
      }

      /deep/ .van-field__control:disabled {
        color: #666 !important;
        -webkit-text-fill-color: unset;
      }
    }

    .van-cell {
      font-size: 12px;
      padding: 16px 12px;

      /deep/ .van-field__control {
        color: #666;
      }
    }

    .form_textarea_wrap {
      margin: 0 12px;
      background-color: #fff;
      padding-bottom: 15px;

      .van-cell {
        font-size: 12px;
        padding: 10px 12px;
        background-color: #f2f2f2;
      }
    }
  }

  .submit {
    padding: 20px 12px;

    .van-button {
      border-radius: 4px;
    }
  }

  .form_block {
    margin-top: 8px;
  }

  .form_title {
    padding: 12px 15px 0px 20px;
    font-size: 15px;
    font-weight: bold;
    color: #444444;
    background-color: #fff;
    position: relative;
    display: flex;
    align-items: center;

    &::before {
      content: "";
      position: absolute;
      width: 2.5px;
      height: 12.5px;
      border-radius: 3px;
      background-color: #1d8deb;
      left: 12px;
    }

    & > .preview {
      font-size: 12px;
      font-weight: 400;
      color: #1d8deb;
      position: absolute;
      right: 15px;
    }
  }
}
</style>



