// TODO 接口

<template>
  <div class="wrapper">
    <order-tab
      :type="type"
      :setType="setTabType"
      :tabs="['全部订单', '待评价', '已完成']"
    />
    <div class="container">
      <order :type="2" :commentType="1" :item="item"></order>
    </div>
    <div class="container">
      <order :type="2" :commentType="4"></order>
    </div>
    <div class="container">
      <order :type="2" :commentType="3" :item="item">
        <template>
          <div class="pay">培训费用 <span>￥ 300</span></div>
          <div class="tiny">
            <span>培训时间:</span>
            <div>
              2020-11-11 14:00 至
              <br />
              2020-11-11 16:00
            </div>
          </div>
          <div class="tiny">
            <span>培训地点:</span>
            <div>福建省厦门市思明区会展中心A101</div>
          </div>
        </template>
      </order>
    </div>
    <div class="container">
      <order :type="2" :commentType="3" :item="item">
        <template>
          <div class="cost">￥ 80</div>
          <div class="pay">查看费用</div>
        </template>
      </order>
    </div>
    <is-bottom></is-bottom>
  </div>
</template>

<script>
import order from "../../components/order";
import orderTab from "../../components/orderTab";
import isBottom from "../../components/isBottom";
// type===1 未评价 type===2 已评价 type=== 0全部
export default {
  metaInfo: {
    title: "我的订单",
  },
  data() {
    return {
      type: 0,
      item: {
        date: "2020.11.11",
        title: "装修咨询公司",
      },
    };
  },
  mounted() {},
  methods: {
    setTabType(type) {
      console.log(type);
      this.type = type;
    },
  },
  components: { order, orderTab, isBottom },
};
</script>

<style scoped lang="scss">
.wrapper {
  background-color: #f2f2f2;
  height: 100%;

  & > .container {
    padding: 12px;
    background-color: #fff;
    margin-bottom: 1px;
  }

  .cost {
    font-weight: bold;
    color: #f25757;
    font-size: 12px;
    margin-bottom: 5px;
  }

  .pay {
    font-size: 12px;
    font-weight: 400;
    color: #1d8deb;
    margin-bottom: 12px;

    span {
      display: inline-block;
      margin-left: 10px;
      color: #f25757;
    }
  }

  .tiny {
    display: flex;
    font-size: 12px;
    color: #444444;

    & > span {
      display: inline-block;
      width: 65px;
      min-width: 65px;
    }
  }
}
</style>



