// TODO 接口

<template>
  <div class="wrapper">
    <img src="" alt="" class="avatar" />
    <div class="tip">
      <div class="name">庄某某 <span>厦门市</span></div>
      <div class="title">专职律师</div>
    </div>
    <div class="line"></div>
    <div class="title">律师简介</div>
    <div class="info">
      知名律所合伙人律师,一流法学中国人大法学院出身,游学美国，卅年办结超两千民商经济案件，积累丰富司法实务经验与技巧，尤为精通法税同审和互联网+金融、房地产等领域，形成缜密务实、专解疑难案件风格。宗旨：服务至上、满意度高。经常总结成功案例并发表法学论文数百万字，代理的成功案例被最高法院评为2013年度全国十大经典知识产权案例，收入最高人民法院官网。（这是文本框输入的内容）
    </div>
  </div>
</template>

<script>
export default {
  metaInfo: {
    title: "我的资料",
  },
  data() {
    return {};
  },
  mounted() {},
};
</script>

<style scoped lang="scss">
.wrapper {
  padding: 12px 12px 25px;
  height: 100%;
  background-color: #fff;

  .avatar {
    width: 100%;
    height: 350px;
    margin-bottom: 12px;
  }

  .tip {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    .name {
      font-size: 15px;
      font-weight: bold;
      color: #444444;

      & > span {
        display: inline-block;
        background-color: #b2dcfe;
        font-size: 12px;
        font-weight: 400;
        color: #666666;
        margin-left: 9px;
        padding: 1px;
      }
    }

    .title {
      font-size: 12px;
      font-weight: 400;
      color: #ed7933;
    }
  }

  .line {
    height: 7.5px;
    width: 100vw;
    margin-left: -12px;
    background-color: #f2f2f2;
  }

  & > .title {
    margin-left: 5px;
    position: relative;
    margin-top: 15px;
    font-size: 15px;
    font-weight: bold;
    color: #444444;
    margin-bottom: 13.5px;

    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 10px;
      background-color: #1d8deb;
      left: -6px;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }

  & > .info {
    text-indent: 2em;
    font-size: 12px;
    font-weight: 400;
    color: #666666;
    line-height: 20px;
  }
}
</style>



