<template>
  <div class="wrapper">
    <div class="head">
      <div class="text">总收入</div>
      <div class="total"><span>￥</span> 3650.00</div>
    </div>
    <div class="th">
      <div>收入金额</div>
      <div>服务类型</div>
      <div>时间</div>
    </div>
    <div class="tr">
      <div>￥666.66</div>
      <div>咨询服务费用</div>
      <div>{{ new Date() | timeFormat }}</div>
    </div>
