      <div>咨询服务费用</div>
      <div>{{ new Date() | timeFormat }}</div>
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  metaInfo: {
    title: "我的资料",
  },
  data() {
    return {};
  },
  mounted() {},
  filters: {
    timeFormat(date) {
      return dayjs(date).format("YYYY.MM.DD");
    },
  },
  methods: {},
};
</script>

<style scoped lang="scss">
.wrapper {
  height: 100%;
  background-color: #fff;

  & > .head {
    background-color: #1d8deb;
    height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 8px;

    .text {
      font-size: 12px;
      color: #ffffff;
      line-height: 16px;
      margin-bottom: 20px;
    }

    .total {
      font-size: 24px;
      font-weight: bold;
      color: #ffffff;
      line-height: 16px;

      span {
        font-size: 18px;
        font-weight: bold;
        color: #feffff;
        line-height: 16px;
      }
    }
  }

  & > .th {
    background: #ebebeb;
    height: 44px;
    display: flex;
    align-items: center;

    div {
      flex: 1;
      text-align: center;
      font-size: 12px;
      font-weight: bold;
      color: #1d8deb;
      line-height: 18px;
    }
  }

  & > .tr {
    display: flex;
    padding: 16px 0;
    align-items: center;
    border-bottom: 1px solid #ebebeb;

    &:last-child {
      border-bottom: 0;
    }

    div {
      font-size: 12px;
      font-weight: 400;
      color: #666666;
      text-align: center;
      flex: 1;
    }
  }
}
</style>



