// TODO 接口 + 路由

<template>
  <div class="wrapper">
    <div class="banner">
      <div class="top_block"></div>
      <div class="container">
        <img src="" alt="" class="avatar" />
        <div class="info">
          <div class="name">厦门秦商信息股份有限公司 <span>VIP</span></div>
          <div class="name">到期时间: 2020.11.11</div>
          <!-- 点击跳转到我的会员页面 ↓-->
          <div class="pay"><span>去充值</span></div>
        </div>
        <icon-setting :style="{position: 'absolute', right: '12px', top: '16px'}"></icon-setting>
      </div>
    </div>
    <div class="list">
      <user-row
        :icon="'order'"
        :title="'我的订单'"
        :onClick="onRowClick"
      ></user-row>
      <user-row
        :icon="'flower'"
        :title="'我的评价'"
        :onClick="onRowClick"
      ></user-row>
      <user-row
        :icon="'retail'"
        :title="'我的分销'"
        :onClick="onRowClick"
      ></user-row>
      <user-row
        :icon="'member'"
        :title="'我的会员'"
        :onClick="onRowClick"
      ></user-row>
    </div>
    <div class="list">
      <user-row
        :icon="'userData'"
        :title="'我的资料'"
        :onClick="onRowClick"
      ></user-row>
    </div>
  </div>
</template>

<script>
import userRow from "../../components/userRow";
import iconSetting from "../../components/icons/setting";

export default {
  metaInfo: {
    title: "我的资料",
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {
    onRowClick() {
      console.log(111);
    },
  },
  components: {
    userRow,
    iconSetting,
  },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  background-color: #f2f2f2;
  flex: 1;

  & > .list {
    margin-top: 8.5px;
  }

  .banner {
    height: 140px;
    position: relative;
    background-color: #fff;
    box-sizing: border-box;
    position: relative;

    & > .top_block {
      height: 90px;
      width: 100%;
      background-color: #0474d1;
    }

    & > .container {
      position: absolute;
      top: 15px;
      bottom: 15px;
      left: 12px;
      right: 12px;
      margin: auto;
      background-color: #1d8deb;
      box-shadow: 2px 2px 13px 0px rgba(136, 136, 136, 0.4);
      border-radius: 4px;
      padding: 15px;
      display: flex;

      & > .avatar {
        width: 60px;
        height: 60px;
        margin-right: 8px;
        border-radius: 50%;
        border: 1px solid #fff;
      }

      & > .info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: left;

        & > .name {
          font-size: 12px;
          color: #ffffff;
          margin-bottom: 5px;

          span {
            display: inline-block;
            width: 35px;
            height: 15px;
            background: linear-gradient(85deg, #fde0b4 0%, #edbb72 100%);
            border-radius: 7.5px;
            text-align: center;
          }
        }

        & > .pay {
          width: 50px;
          height: 15px;
          border: 1px solid #edbb72;
          border-radius: 8px;
          font-size: 12px;
          font-weight: 400;
          color: #edbb72;

          span {
            display: inline-block;
            transform: scale(0.83) translate(7px, -1px);
            text-align: center;
          }
        }
      }
    }
  }
}
</style>



