// TODO 接口 + 路由

<template>
  <div class="wrapper">
    <div class="banner">
      <img src="" alt="" class="avatar" />
      <div class="info">
        <div class="name">张三 <span>资深会计</span></div>
        <div class="phone_number">188888888</div>
      </div>
    </div>
    <div class="list">
      <user-row
        :icon="'order'"
        :title="'我的订单'"
        :onClick="onRowClick"
      ></user-row>
      <user-row
        :icon="'income'"
        :title="'收入明细'"
        :onClick="onRowClick"
      ></user-row>
    </div>
    <div class="list">
      <user-row
        :icon="'userData'"
        :title="'我的资料'"
        :onClick="onRowClick"
      ></user-row>
    </div>
  </div>
</template>

<script>
import userRow from "../../components/userRow";

export default {
  metaInfo: {
    title: "我的资料",
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {
    onRowClick() {
      console.log(111);
    },
  },
  components: {
    userRow,
  },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  background-color: #f2f2f2;
  flex: 1;

  & > .list {
    margin-top: 8.5px;
  }

  .banner {
    display: flex;
    padding: 20px 27px;
    background-color: #1d8deb;
    box-sizing: border-box;

    & > .avatar {
      width: 60px;
      height: 60px;
      margin-right: 8px;
      border-radius: 50%;
    }

    & > .info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: left;

      & > .name {
        font-size: 12px;
        font-weight: 400;
        color: #ffffff;
        line-height: 18px;

        & > span {
          margin-left: 14px;
          font-size: 12px;
          display: inline-block;
          transform: scale(0.85) translateX(-7px);
          color: #edbb72;
        }
      }

      & > .phone_number {
        font-size: 12px;
        font-weight: 400;
        color: #ffffff;
        line-height: 18px;
      }
    }
  }
}
</style>



