import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

let routes = []

const files  = require.context('@/pages', true, /\.vue$/)

files.keys().forEach(value => {
  const path = `/pages${value.substr(value.indexOf('/'), value.lastIndexOf('.') - 1)}`
  const componentPath = value.substr(value.indexOf('.') + 1, value.lastIndexOf('.') - 1)
  const name = componentPath.substr(componentPath.lastIndexOf('/') + 1)
  routes.push({
    path: path,
    name: name,
    component: () => import(`@/pages${componentPath}`)
  })
})

export default new Router({
  mode: 'hash',
  routes: [
    ...routes
  ]
})



