<template>
  <div class="box">
    <div class="item">
      <div class="view">
        <img src="" alt="" />
      </div>
      <div class="content">
        <div class="date">{{ item && item.date }}</div>
        <div class="title">{{ item && item.title }}</div>
        <slot></slot>
        <template v-if="!this.$slots.default">
          <div class="tip">咨询服务费用</div>
          <div class="text">预计支付： <span>￥600</span></div>
          <div class="text">预计支付： <span class="warning">￥300</span></div>
        </template>
      </div>
    </div>
    <div v-if="type === 1" class="evaluate">
      <div class="level">评价等级：<span>一般</span></div>
      <div class="detail">
        {{ formatText(text)
        }}<span @click="showAllText" v-show="text.length > 200 && !isExpand"
          >查看更多...</span
        >
      </div>
    </div>
    <template v-else-if="type === 2">
      <div v-if="commentType === 1" class="comment step_1">
        <span>未开始</span>
      </div>
      <div v-if="commentType === 2" class="comment step_2">
        <span>已结束</span>
      </div>
      <div v-if="commentType === 3" class="comment">
        <span>评价</span>
      </div>
      <div v-if="commentType === 4" class="comment step_4">
        <span>已完成</span>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  // type===1 未评价 type===2 已评价 commentType===1 未开始 commentType===2 已结束 commentType===3 评价 commentType===4 已完成
  props: ["type", "commentType", "item"],
  data() {
    return {
      text: "缜密务实、专解疑难案件风格。宗旨：服务至上、满意度高。",
      isExpand: false,
    };
  },
  mounted() {},
  methods: {
    formatText(text) {
      if (this.isExpand) {
        return text;
      }
      return text?.substr(0, 200);
    },
    showAllText() {
      this.isExpand = true;
    },
  },
};
</script>

<style scoped lang="scss">
.box {
  .comment {
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;

    &.step_1 {
      span {
        background-color: #1d8deb;
        color: #fff;
      }
    }

    &.step_2,
    &.step_4 {
      span {
        background-color: #bfbfbf;
        border: 1px solid #bfbfbf;
        color: #fff;
      }
    }

    span {
      display: inline-block;
      width: 50px;
      line-height: 20px;
      height: 20px;
      border: 1px solid #1d8deb;
      border-radius: 10px;
      font-size: 12px;
      font-weight: 400;
      color: #1d8deb;
      text-align: center;
    }
  }

  & > .evaluate {
    margin-top: 10px;

    .level {
      font-size: 12px;
      font-weight: bold;
      color: #444444;
      margin-bottom: 11px;

      span {
        color: #1d8deb;
      }
    }

    .detail {
      font-size: 12px;
      font-family: Source Han Sans CN;
      font-weight: 400;
      color: #666666;
      line-height: 20px;
      word-break: break-all;

      span {
        color: #1d8deb;
      }
    }
  }

  .item {
    background-color: #fff;
    display: flex;
    justify-content: space-between;

    & > .view {
      width: 120px;
      height: 90px;
      overflow: hidden;
      border-radius: 8px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    & > .content {
      position: relative;
      flex: 1;
      margin-left: 20px;

      .date {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 12px;
        font-weight: 400;
        color: #999999;
      }

      .title {
        font-size: 15px;
        font-weight: bold;
        color: #444444;
        margin-bottom: 6px;
      }

      .tip {
        font-size: 12px;
        font-weight: 400;
        color: #1d8deb;
      }

      .text {
        margin-top: 8px;
        font-size: 12px;
        color: #444444;
      }
    }

    .warning {
      color: #f25757;
    }
  }
}
</style>



