<template>
  <div class="wrapper">
    <div class="container">
      <order :item="item">
        <template>
          <div class="tiny">
            这是机构业务的介绍，各种详情介绍，主营业务，得到的荣誉奖励等等。这是机构的介绍，各种介绍，得到的荣誉奖励等.....
          </div>
          <div class="tiny">指导价： <span>￥600</span></div>
        </template>
      </order>
      <row-line :padding="12" :top="20" :bottom="15"></row-line>
      <div class="detail">
        <div><span>预计支付：</span> ￥600</div>
        <div><span>实际支付：</span> <span>待确认</span></div>
        <div><span>订单类型：</span> 咨询服务费用</div>
        <div><span>订单号：</span> 121232132132123</div>
        <div><span>下单时间：</span> 2020-11-11 14:23:28</div>
        <div><span>评价时间：</span> 2020-11-11 14:23:28</div>
        <div><span>预计支付：</span> 2020-11-11 14:23:28</div>
      </div>
    </div>
  </div>
</template>

<script>
import order from "../../components/order";
import rowLine from "../../components/line";

export default {
  metaInfo: {
    title: "我的订单",
  },
  data() {
    return {
      type: 0,
      item: {
        title: "装修咨询公司",
      },
    };
  },
  mounted() {},
  methods: {
    setTabType(type) {
      console.log(type);
      this.type = type;
    },
  },
  components: { order, rowLine },
};
</script>

<style scoped lang="scss">
.wrapper {
  background-color: #f2f2f2;
  height: 100%;

  .container {
    .detail {
      & > div {
        font-size: 12px;
        font-weight: 400;
        margin-bottom: 14px;
        color: #444444;

        span {
          display: inline-block;
          width: 66px;

          &:nth-child(2) {
            color: #1d8deb;
          }
        }
      }
    }

    margin-top: 8px;
    height: calc(100% - 8px);
    background-color: #fff;
    padding: 15px 12px;

    .tiny {
      font-size: 12px;
      font-weight: 400;
      color: #666666;
      line-height: 20px;
      margin-bottom: 5px;

      span {
        font-size: 14px;
        color: #f25757;
      }
    }
  }
}
</style>



