<template>
  <div class="tab">
    <div v-for="(tab, i) in tabs" :key="tab" :class="type == i && 'active'" @click="setType(i)">
      {{tab}}
    </div>
  </div>
</template>

<script>
export default {
  props: ["type", "setType", "tabs"], // type===0 未评价 type===1 已评价 type=== 2全部
  data() {
    return {};
  },
  mounted() {
    console.log(this.type);
  },
  methods: {},
};
</script>

<style scoped lang="scss">
.tab {
  height: 44px;
  background-color: #e3f2fd;
  display: flex;
  overflow: hidden;

  & > div {
    flex: 1;
    line-height: 44px;
    text-align: center;
    font-size: 15px;
    color: #888;

    &.active {
      color: #1d8deb;
      font-weight: bold;
      background-color: #fff;
      box-shadow: 0px 3px 5px 0px rgba(68, 68, 68, 0.14);
      border-radius: 0px 4px 0px 4px;
    }
  }
}
</style>



