<template>
  <div
    class="line"
    :style="{
      width: `calc(100% - ${(padding || 0) * 2}px)`,
      marginTop: `${top || 0}px`,
      marginBottom: `${bottom || 0}px`,
      height: '1px',
      backgroundColor: '#ebebeb',
    }"
  ></div>
</template>

<script>
export default {
  props: ["padding", "top", "bottom"],
  data() {
    return {};
  },
  mounted() {},
  methods: {},
};
</script>

<style scoped lang="scss">
</style>



