// TODO 接口

<template>
<!-- setting 设置密码 -->
  <div class="wrapper setting">
    <van-form @submit="onSubmit">
      <!-- 设置页面需要隐藏旧密码 ↓ -->
      <div class="form_block">
        <div class="form_item">
          <van-field
            v-model="password"
            name="修改手机号"
            placeholder="请输入旧密码"
            type="password"
          >
            <template #label><lock /></template>
          </van-field>
        </div>
      </div>
      <div class="form_block">
        <div class="form_item">
          <van-field
            v-model="password"
            name="修改手机号"
            placeholder="设置密码（6-20位数字与字母组合）"
            type="password"
          >
            <template #label><lock /></template>
          </van-field>
        </div>
      </div>
      <div class="form_block">
        <div class="form_item">
          <van-field
            v-model="password"
            name="修改手机号"
            placeholder="再次输入新密码"
            type="password"
          >
            <template #label><lock /></template>
          </van-field>
        </div>
      </div>
      <div class="submit">
        <van-button type="info" size="large" native-type="submit" :disabled="!password"
          >完成</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import arrowRight from "../../../components/icons/arrowRight";
import lock from "../../../components/icons/lock";
import { Toast } from "vant";

export default {
  metaInfo: {
    title: "修改手机号",
  },
  data() {
    return {
      password: "",
    };
  },
  mounted() {},
  methods: {
    onSubmit(value) {
      console.log(value);
    },
    getCode() {
      if (this.number) {
        Toast("验证码已发送");
      } else {
        Toast("请输入正确的手机号");
      }
    },
  },
  components: { arrowRight, lock },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  height: 100%;
  padding-top: 16px;
  position: relative;
  background-color: #f2f2f2;

  .submit {
    position: absolute;
    bottom: 15px;
    left: 15px;
    right: 15px;
  }

  .van-button {
    height: 40px;
    width: calc(100% - 30px);
    margin: 30px 15px;
    font-size: 15px;
    font-weight: 400;
  }

  .form_block {
    margin-bottom: 12px;
  }

  .van-cell {
    .van-field--disabled .van-field__label {
      color: #444;
    }

    /deep/ .van-field__label {
      width: 30px;
      display: flex;
      align-items: center;
    }
  }
  .form_item {
    background-color: #fff;

    .van-field--disabled {
      /deep/ .van-cell__title {
        color: #444;
      }

      /deep/ .van-field__control {
        color: #666;
      }
    }

    .van-cell {
      font-size: 13px;
      padding: 8px 30px 8px 12px;

      /deep/ .van-field__control {
        color: #666;
      }
    }
  }
}
</style>



