<template>
  <div class="bottom" v-if="hasScroll">已经到底啦~</div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      hasScroll: false,
    };
  },
  watch: {
    list() {
      console.log(this.hasScrollbar());
      this.hasScroll = this.hasScrollbar();
    },
  },
  methods: {
    hasScrollbar() {
      return (
        (document.querySelector(".wrapper") || document.body).scrollHeight >
        (window.innerHeight || document.documentElement.clientHeight)
      );
    },
  },
};
</script>

<style scoped lang="scss">
.bottom {
  height: 41.5px;
  width: 100%;
  line-height: 41.5px;
  text-align: center;
  font-size: 12px;
  color: #999999;
  background-color: #f2f2f2;
}
</style>



