// TODO 接口

<template>
  <div class="wrapper">
    <order-tab
      :type="type"
      :setType="setTabType"
      :tabs="['全部订单', '待评价', '已完成']"
    />
    <div class="container">
      <order></order>
    </div>
    <div class="container">
      <order :type="'2'"></order>
    </div>
  </div>
</template>

<script>
import order from "../../components/order";
import orderTab from "../../components/orderTab";
// type===1 未评价 type===2 已评价 type=== 0全部
export default {
  metaInfo: {
    title: "我的订单",
  },
  data() {
    return {
      type: 0,
    };
  },
  mounted() {},
  methods: {
    setTabType(type) {
      console.log(type);
      this.type = type;
    },
  },
  components: { order, orderTab },
};
</script>

<style scoped lang="scss">
.wrapper {
  background-color: #f2f2f2;
  height: 100%;

  & > .container {
    padding: 12px;
    background-color: #fff;
    margin-bottom: 1px;
  }
}
</style>



