// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueMeta from 'vue-meta'
import api from "./api"; // 接口调用
import Vuex from "vuex"
import { Form, Field, Button, RadioGroup, Radio, Col, Row, Popup } from 'vant';

Vue.prototype.$api = api;

// 管理头部title
Vue.use(VueMeta)
Vue.use(Vuex)
Vue.use(Form)
Vue.use(Field)
Vue.use(Button)
Vue.use(RadioGroup)
Vue.use(Radio)
Vue.use(Col)
Vue.use(Row)
Vue.use(Popup)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  api
})



