// TODO 接口 + 路由

<template>
  <div class="wrapper">
    <div class="banner">
      <img src="" alt="" class="avatar" />
      <div class="info">
        <div class="name">正大无极有限公司</div>
        <div class="call">办公家具咨询</div>
        <div class="phone_number">188888888</div>
      </div>
    </div>
    <div class="list">
      <user-row
        :icon="'userData'"
        :title="'我的资料'"
        :onClick="onRowClick"
      ></user-row>
    </div>
  </div>
</template>

<script>
import userRow from "../../components/userRow";

export default {
  metaInfo: {
    title: "我的资料",
  },
  data() {
    return {};
  },
  mounted() {},
  components: { userRow },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  background-color: #f2f2f2;
  flex: 1;

  & > .list {
    margin-top: 8.5px;
  }

  .banner {
    display: flex;
    padding: 20px 27px;
    background-color: #1d8deb;
    box-sizing: border-box;

    & > .avatar {
      width: 60px;
      height: 60px;
      margin-right: 8px;
      border-radius: 50%;
    }

    & > .info {
      transform: translateY(6px);
      text-align: left;

      & > .name {
        font-size: 12px;
        font-weight: 400;
        color: #ffffff;
        line-height: 18px;
        margin-bottom: 3px;
      }

      & > .call {
        font-size: 12px;
        font-weight: 400;
        color: #edbb72;
        line-height: 18px;
        transform: scale(0.85) translateX(-7px);
      }

      & > .phone_number {
        font-size: 12px;
        font-weight: 400;
        color: #ffffff;
        line-height: 18px;
      }
    }
  }
}
</style>



