// TODO 接口 + 详细表单UI

<template>
  <!-- view 预览  -->
  <div class="wrapper view">
    <van-form @submit="onSubmit" disabled>
      <div class="form_block">
        <div class="form_item">
          <van-field
            v-model="name"
            name="修改手机号"
            label="修改手机号"
            placeholder="请输入"
            input-align="right"
          >
            <template #right-icon>
              <arrow-right
                :iconStyle="{
                  position: 'absolute',
                  right: '-18px',
                  top: '7px',
                }"
              ></arrow-right>
            </template>
          </van-field>
          <van-field
            name="设置密码"
            label="设置密码"
            placeholder="去设置"
            input-align="right"
          >
            <template #right-icon
              ><arrow-right
                :iconStyle="{
                  position: 'absolute',
                  right: '-18px',
                  top: '7px',
                }"
              ></arrow-right
            ></template>
          </van-field>
        </div>
      </div>
      <div class="logout">退出登录</div>
    </van-form>
  </div>
</template>

<script>
import arrowRight from "../../../components/icons/arrowRight";

export default {
  metaInfo: {
    title: "我的简历",
  },
  data() {
    return {
      name: "111",
    };
  },
  mounted() {},
  methods: {
    onSubmit(value) {
      console.log(value);
    },
  },
  components: { arrowRight },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  background-color: #f2f2f2;
  height: 100%;

  .logout {
    margin-top: 7.5px;
    background-color: #fff;
    height: 44px;
    text-align: center;
    line-height: 44px;
    font-size: 15px;
    font-weight: 400;
    color: #444444;
  }

  .form_block {
    margin-top: 8px;
  }

  .line {
    width: calc(100vw - 24px);
    margin-left: 12px;
    height: 1px;
    background: #ebebeb;
  }

  &.view {
    padding-bottom: 20px;

    .submit {
      display: none;
    }

    .preview {
      display: none;
    }
  }

  .van-cell {
    .van-field--disabled .van-field__label {
      color: #444;
    }
  }
  .form_item {
    background-color: #fff;

    .van-field--disabled {
      /deep/ .van-cell__title {
        color: #444;
      }

      /deep/ .van-field__control {
        color: #666;
      }
    }

    .van-cell {
      font-size: 12px;
      padding: 16px 30px 16px 12px;

      /deep/ .van-field__control {
        color: #666;
      }
    }
  }
}
</style>



