// TODO 接口 + 手机的区号

<template>
  <div class="wrapper">
    <div class="current_phone">
      <div class="current">当前绑定手机号</div>
      <div class="number">121 **** 6512</div>
    </div>
    <van-form @submit="onSubmit">
      <div class="form_block">
        <div class="form_item">
          <van-field
            v-model="number"
            name="修改手机号"
            placeholder="请输入手机号"
            :clearable="true"
          />

          <van-field name="设置密码" placeholder="输入手机验证码">
            <template #extra>
              <span class="code" @click="getCode">获取验证码</span>
            </template>
          </van-field>
        </div>
      </div>
      <van-button type="info" size="large" native-type="submit" disabled
        >确认修改手机号</van-button
      >
    </van-form>
  </div>
</template>

<script>
import arrowRight from "../../../components/icons/arrowRight";
import { Toast } from "vant";

export default {
  metaInfo: {
    title: "修改手机号",
  },
  data() {
    return {
      number: "18198745612",
      passport: "",
    };
  },
  mounted() {},
  methods: {
    onSubmit(value) {
      console.log(value);
    },
    getCode() {
      if (this.number) {
        Toast("验证码已发送");
      } else{
        Toast("请输入正确的手机号")
      }
    },
  },
  components: { arrowRight },
};
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;

  .van-button {
    height: 40px;
    width: calc(100% - 30px);
    margin: 30px 15px;
    font-size: 15px;
    font-weight: 400;
  }

  .current_phone {
    display: flex;
    justify-items: center;
    align-items: center;
    flex-direction: column;
    margin-top: 15px;
    margin-bottom: 20px;

    .current {
      font-size: 12px;
      color: #888;
      line-height: 24px;
    }
  }

  .code {
    font-size: 12px;
    color: #1d8deb;
  }

  .logout {
    margin-top: 7.5px;
    background-color: #fff;
    height: 44px;
    text-align: center;
    line-height: 44px;
    font-size: 15px;
    font-weight: 400;
    color: #444444;
  }

  .form_block {
    margin-top: 8px;
  }

  .line {
    width: calc(100vw - 24px);
    margin-left: 12px;
    height: 1px;
    background: #ebebeb;
  }

  &.view {
    padding-bottom: 20px;

    .submit {
      display: none;
    }

    .preview {
      display: none;
    }
  }

  .van-cell {
    .van-field--disabled .van-field__label {
      color: #444;
    }
  }
  .form_item {
    background-color: #fff;

    .van-field--disabled {
      /deep/ .van-cell__title {
        color: #444;
      }

      /deep/ .van-field__control {
        color: #666;
      }
    }

    .van-cell {
      font-size: 15px;
      padding: 16px 30px 16px 12px;

      /deep/ .van-field__control {
        color: #666;
      }
    }
  }
}
</style>



