// TODO 接口

<template>
  <div class="wrapper">
    <order-tab
      :type="type"
      :setType="setTabType"
      :tabs="['待评价', '已评价']"
    />
    <div class="container">
      <order :type="1" :commentType="3" :item="item"> </order>
    </div>
      <div class="container">
      <order :type="2" :commentType="3" :item="item"> </order>
    </div>
    <is-bottom :list="list"></is-bottom>
  </div>
</template>

<script>
import order from "../../components/order";
import orderTab from "../../components/orderTab";
import isBottom from "../../components/isBottom";
// type===1 未评价 type===2 已评价 type=== 0全部
export default {
  metaInfo: {
    title: "我的订单",
  },
  data() {
    return {
      list: [],
      type: 0,
      item: {
        date: "2020.11.11",
        title: "装修咨询公司",
      },
    };
  },
  mounted() {
    let index = 0;
    setInterval(() => {
      this.list.push(index);
      index++;
    }, 100000);
  },
  methods: {
    setTabType(type) {
      console.log(type);
      this.type = type;
    },
  },
  components: { order, orderTab, isBottom },
};
</script>

<style scoped lang="scss">
.wrapper {
  background-color: #f2f2f2;

  & > .container {
    padding: 12px;
    background-color: #fff;
    margin-bottom: 1px;
  }

  .cost {
    font-weight: bold;
    color: #f25757;
    font-size: 12px;
    margin-bottom: 5px;
  }

  .pay {
    font-size: 12px;
    font-weight: 400;
    color: #1d8deb;
    margin-bottom: 12px;

    span {
      display: inline-block;
      margin-left: 10px;
      color: #f25757;
    }
  }

  .tiny {
    display: flex;
    font-size: 12px;
    color: #444444;

    & > span {
      display: inline-block;
      width: 65px;
      min-width: 65px;
    }
  }
}
</style>



