<template>
  <div
    class="block"
    :style="{
      height: `${height}px`,
      backgroundColor: `${color}`
    }"
  ></div>
</template>

<script>
export default {
  props: ["height", "color"],
  data() {
    return {};
  },
  watch: {},
  methods: {},
};
</script>

<style scoped lang="scss">
.block {
  background-color: #f2f2f2;
  width: 100%;
  height: 8px;
}
</style>



