// TODO 接口

<template>
  <div class="wrapper">
    <block></block>
    <div class="container">
      <van-form>
        <div class="ass_row">
          <span>评价等级</span>
          <van-radio-group v-model="assessment" direction="horizontal">
            <van-radio name="1">好</van-radio>
            <van-radio name="2">较好</van-radio>
            <van-radio name="3">一般</van-radio>
            <van-radio name="4">差</van-radio>
          </van-radio-group>
        </div>
        <div class="tip">
          （系统会根据您的评价等级支付费用。好100%
          、较好95%、一般90%、差85%，请认真评价！）
        </div>
        <div class="title">评价详情</div>
        <van-field type="textarea" rows="10"> </van-field>

        <div class="submit">
          <van-button type="info" size="large" native-type="submit"
            >提交</van-button
          >
        </div>
      </van-form>
    </div>
    <van-popup v-model="show">
      <div class="success_modal">
        <div class="head">
          <div class="right">
            <svg
              t="1615992841736"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2756"
              width="200"
              height="200"
            >
              <path
                d="M75.712 445.712l240.176 185.52s13.248 6.624 29.808 0l591.36-493.872s84.272-17.968 68.64 71.488c-57.04 57.968-638.464 617.856-638.464 617.856s-38.096 21.536-74.544 0C256.272 790.256 12.816 523.568 12.816 523.568s-6.672-64.592 62.896-77.856z"
                p-id="2757"
                fill="#1d8deb"
              ></path>
            </svg>
          </div>
          <span>提交成功</span>
        </div>
        <div class="body">
          <div>感谢您的认真评价，您还可以去及时 评价其他订单哦！</div>
          <span @click="closeModal">好的</span>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import block from "../../components/block";
export default {
  metaInfo: {
    title: "发表评价",
  },
  data() {
    return {
      show: true,
    };
  },
  mounted() {},
  methods: {
    closeModal() {
      this.show = false;
    },
  },
  components: { block },
};
</script>

<style scoped lang="scss">
.wrapper {
  & > .container {
    padding: 15px 12px;

    .van-form {
      & > .van-cell {
        padding: 0;
        font-size: 12px;
        line-height: 20px;
        color: #666666;

        /deep/ textarea {
          background-color: #f2f2f2;
          padding: 9px 10px;
        }
      }
    }

    .ass_row {
      display: flex;
      align-items: center;

      & > span {
        font-size: 12px;
        font-weight: bold;
        color: #444;
        margin-right: 20px;
        display: inline-block;
      }
    }

    .submit {
      padding: 20px 0px;

      .van-button {
        border-radius: 4px;
      }
    }

    .tip {
      font-size: 12px;
      color: #999999;
      line-height: 15px;
      margin-top: 11.5px;
      margin-bottom: 20px;
    }

    .title {
      font-size: 12px;
      font-weight: bold;
      color: #444444;
      margin-bottom: 8px;
    }

    .van-radio {
      margin-right: 20px;
      font-size: 12px;

      /deep/ .van-radio__icon--checked {
        .van-icon::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          width: 5px;
          height: 5px;
          background-color: #1989fa;
          border-radius: 50%;
        }
      }

      /deep/ .van-radio__icon {
        display: flex;
        align-items: center;

        /deep/ &.van-radio__icon--checked {
          position: relative;
        }

        /deep/ .van-icon {
          width: 0.8em;
          height: 0.8em;
          background-color: #fff;
        }
      }
    }
  }

  .van-popup {
    border-radius: 5px;
  }

  .success_modal {
    width: calc(100vw - 96px);
    background: #ffffff;
    position: relative;

    .body {
      padding: 16px 39px 20px;
      background-color: #fff;
      text-align: center;

      & > div {
        text-align: center;
        line-height: 22px;
        font-size: 13px;
        font-weight: 400;
        color: #666666;
        margin-bottom: 30px;
      }

      & > span {
        display: inline-block;
        width: 150px;
        height: 30px;
        background: #1d8deb;
        border-radius: 15px;
        line-height: 30px;
        color: #fff;
      }
    }

    .head {
      height: 62px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #1d8deb;
      width: 100%;
      .right {
        width: 30px;
        height: 30px;
        background-color: #fff;
        border-radius: 50%;
        text-align: center;
        line-height: 33px;
        margin-right: 8px;

        svg {
          width: 20px;
          height: 15px;
        }
      }

      span {
        font-size: 18px;
        font-weight: 400;
        color: #ffffff;
      }
    }
  }
}
</style>



